.page__body {
  font-family: "PT Mono", "Courier New", monospace;
  color: #041827;
  background-color: #f8fdff;
  min-width: 320px;
  width: 100%; }
  .page__body--scroll-lock {
    color: transparent; }
    @media (max-width: 767px) {
      .page__body--scroll-lock {
        position: relative;
        overflow-y: hidden;
        height: 100vh; }
        .page__body--scroll-lock::before {
          content: "";
          position: absolute;
          width: 100vw;
          height: 100vh;
          background-color: rgba(4, 24, 39, 0.5);
          z-index: 1; } }

.social__item {
  margin-bottom: 25px; }

.social__item a {
  display: block;
  width: 30px;
  height: 30px; }
  .social__item a:hover, .social__item a:focus {
    outline: 2px solid white;
    outline-offset: 4px;
    border-radius: 2px; }

.social__item svg {
  fill: #f8fdff; }
  .social__item svg:hover, .social__item svg:focus {
    opacity: 0.8; }

