.intro-main {
  min-height: 880px;
  background: $color-black-pearl-lite url("../img/jpg/bg-intro-desktop@1x.jpg") no-repeat bottom;;
  background-size: cover;
  overflow: auto;

  @include vp-1024 {
    @include retina {
      background-image: url("../img/jpg/bg-intro-desktop@2x.jpg");
    }
  }

  @include vp-1023 {
    min-height: 640px;
    background-image: url("../img/jpg/bg-intro-tablet@1x.jpg");

    @include retina {
      background-image: url("../img/jpg/bg-intro-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    min-height: 560px;
    background-image: url("../img/jpg/bg-intro-mobile@1x.jpg");

    @include retina {
      background-image: url("../img/jpg/bg-intro-mobile@2x.jpg");
    }
  }
}

.intro-main__wrapper {
  position: relative;
  padding-left: 165px;
  margin-top: 305px;
  margin-bottom: 40px;

  @include vp-1023 {
    margin-top: 180px;
  }

  @include vp-767 {
    margin-top: 150px;
  }
}

.intro-main h2 {
  margin: 0 auto 31px;
  font-family: $ff-roboto-condensed;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  color: $color-alice-blue;
  text-transform: uppercase;
  letter-spacing: 0.07em;

  @include vp-1023 {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 0;
  }

  @include vp-767 {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 8px;
  }
}

.intro-main p {
  width: 690px;
  font-size: 22px;
  line-height: 34px;
  color: $color-alice-blue;
  text-transform: lowercase;
  margin: 0 0 47px;

  @include vp-1023 {
    width: auto;
    margin-bottom: 57px;
  }

  @include vp-767 {
    margin-bottom: 44px;
    font-size: 14px;
    line-height: 21.7px;
  }
}

.intro-main__button {
  margin: 0;
  font-weight: 700;

  @include vp-1023 {
    margin-bottom: 57px;
    margin-left: 46px;
  }

  @include vp-767 {
    margin-left: 0;
    margin-bottom: 10px;
    text-align: center;
  }
}
